:root,
html[data-theme=light] {
  box-sizing: border-box;
  --design-animation-default: all 0.2s ease-in-out;
  --border-radius: .3rem;
  --base-unit: 1rem;
  --font-text: "IBM Plex Sans",
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Roboto,
    Helvetica,
    Arial,
    sans-serif,
    Apple Color Emoji,
    Segoe UI Emoji,
    Segoe UI Symbol;
  --font-headings: "IBM Plex Sans",
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Roboto,
    Helvetica,
    Arial,
    sans-serif,
    Apple Color Emoji,
    Segoe UI Emoji,
    Segoe UI Symbol;
  --font-mono: "SFMono-Regular",
    Consolas,
    "Liberation Mono",
    Menlo,
    Courier,
    monospace;
}

:root {
  --color-background-default: #fff;
  --color-background-default-contrast: #161718;
  --color-text-primary: #161718;
  --color-text-primary-contrast: #e5e5e5;
  --color-headings-primary: #161718;
  --color-link-primary: #3300f7;
  --color-link-hover: crimson;
  --color-link-current: black;
  --color-highlight-default: aliceblue;
  --color-highlight-extra: rgb(249 249 249);
  --color-highlight-warn: #f3e4e4;
  --color-highlight-friendly: linear-gradient(45deg, rgb(225 255 229), rgb(241 238 206));
  --color-highlight-friendly-text: #285428;
  --color-highlight-gray: #ebecec;
  --color-highlight-special: rgb(17 220 175 / 14%);
  --design-shadow-primary: rgba(84, 102, 120, 0.396) 0px 20px 30px -10px;
  --show-modifier-light: initial;
  --show-modifier-dark: none;
  transition: var(--design-animation-default);
  --widthMidi: calc(var(--base-unit)*70);
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  color: var(--color-text-primary);
  font-family: var(--font-text);
  font-weight: 400;
  font-style: normal;
  font-size: var(--base-unit);
  background: var(--color-background-default);
  hyphens: auto;
}

a {
  text-decoration: none;
}

// Menubar

.menuBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: calc(var(--base-unit)*4) calc(var(--base-unit)*7);

  &__logotype__image {
    height: calc(var(--base-unit)*4);
  }

  &__nav {
    ul {
      list-style-type: none;
      padding: 0;
      margin: 0;
      font-weight: 500;

      li {
        display: inline-block;

        &:not(:last-of-type) {
          margin-right: calc(var(--base-unit)*1);
        }
      }
    }
  }
}

// Intro section

.introBox {
  height: 30vh;
  display: flex;
  align-items: end;

  &__text {
    padding: calc(var(--base-unit)*4);
    max-width: var(--widthMidi);
    margin: 0 auto;

    h1 {
      font-size: calc(var(--base-unit)*1.7);
      margin: 0;

      &::after {
        content: "";
        display: block;
        width: calc(var(--base-unit)*4);
        height: calc(var(--base-unit)*.2);
        background-color: var(--color-link-primary);
        margin: calc(var(--base-unit)*.8) 0;
      }
    }

    p {
      font-size: calc(var(--base-unit)*1.7);
      font-weight: 400;
      margin-top: 0;
      line-height: 160%;
    }
  }
}

// Spectrum matrix

.spectrumMatrix {
  background-image:
    linear-gradient(to bottom,
      #fff 50%,
      #4A4E51 50%);

  &__box {
    max-width: var(--widthMidi);
    margin: 0 auto;
  }
}

.singleSpectrum {
  min-height: calc(var(--base-unit)*25);
  background-size: cover;
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas: "one two three";
  display: relative;
  z-index: 0;

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: calc(var(--base-unit)*2);
    z-index: 10;
    transition: var(--design-animation-default);

    &:hover {
      transform: scale(1.04);
    }
  }
}

.singleSpectrum:nth-of-type(1) {
  .singleSpectrum__text {
    grid-area: one;
    background-color: #0208a1;
    color: #F2F2F2;
  }
}

.singleSpectrum:nth-of-type(2) {
  .singleSpectrum__text {
    grid-area: two;
    background-color: #9e00ff;
    color: #F2F2F2;
  }
}

.singleSpectrum:nth-of-type(3) {
  .singleSpectrum__text {
    grid-area: three;
    background-color: #141414;
    color: #e2e2e2;
  }
}

// Green Box

.greenBox {
  background: #4A4E51;
  overflow: auto;
  padding: calc(var(--base-unit)*4) calc(var(--base-unit)*7);

  &__message {
    width: var(--widthMidi);
    margin: 0 auto;
    display: grid;
    grid-auto-columns: 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    align-items: center;
  }

  &__image {
    img {
      max-width: calc(var(--base-unit)*30);
    }

    grid-area: 1 / 2 / 2 / 3;
  }

  &__text {
    margin: 0 auto;
    grid-area: 1 / 1 / 2 / 4;

  }

  h1 {
    font-size: calc(var(--base-unit)*1.7);
    text-align: center;
    color: #08AF91;
          margin: 0;
          &::after {
            content: "";
            display: block;
            width: calc(var(--base-unit)*4);
            height: calc(var(--base-unit)*.2);
            background-color: #08AF91;
            margin: calc(var(--base-unit)*.8) auto;
          }
  }

  p {
    text-align: center;
    font-size: calc(var(--base-unit)*1.7);
    color: var(--color-text-primary-contrast)
  }
}